.card {
    height: auto;
    width: 340px;
    min-width: 340px;
    margin: 0 auto;
    &__wrap {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        position: relative;
        width: 100%;
        &__slider {
            &__wrapBtn {
                display: flex;
                align-items: center;
                justify-content: center; }
            &__btn {
                display: block;
                background: transparent;
                border: 0;
                outline: 0;
                cursor: pointer;
                z-index: 99; } } } }


@media screen and (max-width: 992px) {
    .card__wrap__slider__btn {
        &__left {
            top: 100px;
            left: 0; }
        &__right {
            top: 100px;
            right: 0; } } }
